<template>
	<div class="newCustomer-page input-border-none self-mt-header self-vant-file " v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <header class="post-head">
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">基本信息</span>
        </header>
		<!-- <mt-header title="基本信息" fixed>
            <router-link to="/qdsPersonal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
      
        <div class="zhanwei"></div>
        <div class="infor-write">
            <div>
                <van-cell-group >   
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.userDataName" required :disabled='disabled.userDataName'  clearable label="姓名"  placeholder="请输入姓名" />
                    </div>       
                    <div class="item-file">
                        <div class="item-file-child" @click='selectSex'></div>
                        <van-field v-model.trim="ruleCompanyAuth.chanceUserSex"  disabled  clearable label="性别" right-icon="arrow" placeholder="请选择性别" @click-right-icon="selectSex" />
                    </div>  
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.userDataPost"  :disabled='disabled.userDataPost'   clearable label="职位"  placeholder="请输入职位" />
                    </div>           
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.userDataEmail"  :disabled='disabled.userDataEmail'   clearable label="邮箱"  placeholder="请输入邮箱" />
                    </div>           
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.userDataWx"  :disabled='disabled.userDataWx'   clearable label="微信"  placeholder="请输入微信号" />
                    </div>           
                    <div class="item-file">
                          <van-field v-model.trim="ruleCompanyAuth.userDataQq"  :disabled='disabled.userDataQq'   clearable label="QQ"  placeholder="请输入QQ号" />
                    </div>           
                    <div class="item-file">
                          <van-field v-model.trim="ruleCompanyAuth.userDataSinaWb"  :disabled='disabled.userDataSinaWb'   clearable label="新浪微博"  placeholder="请输入新浪微博" class="weibo"/>
                    </div>          
                    <div class="bg"></div>
                    <div class="bg-title">
                        <p>发票信息</p>
                    </div>
                    <div class="item-file">
                        <van-field v-model.trim="ruleCompanyAuth.companyName" required :disabled='disabled.companyName'   clearable label="公司名称"  placeholder="请输入公司名称" />
                    </div>   
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.companyCode" required :disabled='disabled.companyCode'   clearable label="税号"  placeholder="请输入税号" />
                    </div>   
                    <div class="item-file">
                        <van-field v-model.trim="ruleCompanyAuth.companyAddress"  :disabled='disabled.companyAddress'   clearable label="地址"  placeholder="请输入地址" />
                    </div>   
                    <div class="item-file">
                        <van-field v-model.trim="ruleCompanyAuth.companyStartBank"  :disabled='disabled.companyStartBank'   clearable label="开户行"  placeholder="请输入开户行" />
                    </div>   
                    <div class="item-file">
                        <van-field v-model.trim="ruleCompanyAuth.companyBankAccount"  :disabled='disabled.companyBankAccount'   clearable label="开户行账号"  placeholder="请输入开户行账号" />
                    </div>   
                </van-cell-group>
            </div>
            <div class="zhanwei-bottom"></div>
            <div class="stepTwo">
                <div class="next-one" @click="saveInfor">
                    <p>{{btnText}}</p>
                </div>
            </div>
            <van-picker class="fixed-pop" v-show="showSex" :columns="sexColumns"  show-toolbar title="性别"  @cancel="sexCancel" @confirm="sexConfirm"/>
        </div>
	</div>
  
</template>
<script>
import Util from '@/js/mUtils';
import { ImagePreview , Notify} from 'vant';
import { MessageBox } from 'mint-ui';
import {DLDict,DLArea,DLTypeList,DLNatureList,DLSizeList} from '@/js/dl-code';
export default {
	data() {
		return{
			globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loading: false,
			ruleCompanyAuth:{
                userDataName: '',    // 姓名
                chanceUserSex: '',    // 性别
                userDataPost: '',     // 职位
                userDataEmail:'',     // email
                userDataWx: '',       // 微信
                userDataQq:'',       // QQ
                userDataSinaWb:'',    // 新浪微博

                companyName:'',    // 公司名称
                companyCode:'',    // 税号
                companyAddress:'',   // 公司地址
                companyStartBank:'',   // 开户行
                companyBankAccount:'',   // 开户行账号
            },
            disabled: {         // 如果该企业在该企业库中存在, 则后端返回企业信息, 可能存在空字段, 空字段可以修改, 不为空则不可修改
                userDataName: true,    // 姓名
                chanceUserSex: true,    // 性别
                userDataPost: true,     // 职位
                userDataEmail: true,     // email
                userDataQq: true,       // QQ
                userDataWx: true,
                userDataSinaWb: true,    // 新浪微博

                companyName: true,    // 公司名称
                companyCode: true,    // 税号
                companyAddress: true,   // 公司地址
                companyStartBank: true,   // 开户行
                companyBankAccount: true,   // 开户行账号
            }, 
            sexColumns:['男', '女'],
            showSex: false,
            clientHeight: {
				height: ""
            },
            btnText: '编辑',
		}
	},
	mounted() {
        this.getUserBasicData()
        this.getChannelUserInvoice()
	},
	components: {
        
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
	methods: {
         backHistory() {
            window.history.back(-1)
        },
        getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        selectSex() {   // 显示企业规模列表
            if(this.disabled.chanceUserSex){
                return false
            }else {
                this.showSex = true
            }
            
        },
        sexConfirm(value, index) {   // 确定选择企业规模
            let that = this
            that.showSex = false
            that.ruleCompanyAuth.chanceUserSex = value
        },
        sexCancel() {            // 取消 企业规模
            this.showSex = false
            // Toast('取消');
        },
        getUserBasicData() {        //获取用户基础信息
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'getChannelUser?token=' + token,{
                method: 'post',
                timeout: 10000
            })
            .then( response=> {
                if(response.data.code == "200") {
                    if(response.data.entity.userName) {
                        that.ruleCompanyAuth.userDataName = response.data.entity.userName;
                    }
                    if(response.data.entity.gender) {
                        if(response.data.entity.gender == 1) {
                            that.ruleCompanyAuth.chanceUserSex = '男'
                        }else if(response.data.entity.gender == 2) {
                            that.ruleCompanyAuth.chanceUserSex = '女'
                        }
                    }
                    if(response.data.entity.position) {
                        that.ruleCompanyAuth.userDataPost = response.data.entity.position;
                    }
                    if(response.data.entity.email) {
                        that.ruleCompanyAuth.userDataEmail = response.data.entity.email;
                    }
                    if(response.data.entity.wechatName) {
                        that.ruleCompanyAuth.userDataWx = response.data.entity.wechatName;
                    }
                    if(response.data.entity.qqName) {
                        that.ruleCompanyAuth.userDataQq = response.data.entity.qqName;
                    }
                    if(response.data.entity.blogName) {
                        that.ruleCompanyAuth.userDataSinaWb = response.data.entity.blogName;
                    }
                   
                }else {
                    Notify({
                        message: '获取用户基础数据失败',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch(error => {

            })
        },
        getChannelUserInvoice() {               //获取发票信息
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.axios(that.qdsGlobalApiUrl + 'getChannelUserInvoice?token=' + token, {
                methods: 'post',
                timeout: 10000
            })
            .then((response) => {
                if(response.data.code == "200") {
                    if(response.data.entity.companyName) {
                        that.ruleCompanyAuth.companyName = response.data.entity.companyName
                    }
                    if(response.data.entity.companyCode) {
                        that.ruleCompanyAuth.companyCode = response.data.entity.companyCode
                    }
                    if(response.data.entity.companyAddress) {
                        that.ruleCompanyAuth.companyAddress = response.data.entity.companyAddress
                    }
                    if(response.data.entity.companyPhone) {
                        that.ruleCompanyAuth.companyPhone = response.data.entity.companyPhone
                    }
                    if(response.data.entity.companyStartBank) {
                        that.ruleCompanyAuth.companyStartBank = response.data.entity.companyStartBank
                    }
                    if(response.data.entity.companyBankAccount) {
                        that.ruleCompanyAuth.companyBankAccount = response.data.entity.companyBankAccount
                    }
                }else {
                   Notify({
                        message: '获取发票信息失败',
                        duration: 2000,
                        color: 'FF6262'
                    })
                }
            })
            .catch((error) => {

            })
        },
        saveInfor() {   // 保存按钮
            var that = this
            if(that.btnText == '编辑'){
                that.btnText = '保存'
                that.disabled = {        
                    userDataName: false,    // 姓名
                    chanceUserSex: false,    // 性别
                    userDataPost: false,     // 职位
                    userDataEmail: false,     // email
                    userDataQq: false,       // QQ
                    userDataSinaWb: false,    // 新浪微博

                    companyName: false,    // 公司名称
                    companyCode: false,    // 税号
                    companyAddress: false,   // 公司地址
                    companyStartBank: false,   // 开户行
                    companyBankAccount: false,   // 开户行账号
                }
            }else if(that.btnText == '保存') {
                that.userDataSave()
            }
        },
       
        userDataSave() {        //用户基本信息保存
            var that = this;
            var token = Util.getCookie('qdsMToken');
            if(that.ruleCompanyAuth.userDataName == '') {
                Notify({
                    message: '姓名仅允许汉字与字母',
                    duration: 3000,
                    background: '#E6A23C'
                });
                return false;
            }else if(!(/^[a-zA-Z\u4e00-\u9fa5]+$/.test(that.ruleCompanyAuth.userDataName))){
                 Notify({
                    message: '姓名仅允许汉字与字母',
                    duration: 3000,
                    background: '#E6A23C'
                });
                return false;
            }
            if(that.ruleCompanyAuth.userDataQq.length > 0) {
                if(!(/^[1-9]\d*$/.test(that.ruleCompanyAuth.userDataQq))){
                     Notify({
                        message: '请填写正确的QQ号格式',
                        duration: 3000,
                        background: '#E6A23C'
                    });
                    return false;
                }
            }
            if(that.ruleCompanyAuth.companyName.length == 0) {
                Notify({
                    message: '请填写企业名称',
                    duration: 3000,
                    background: '#E6A23C'
                });
                return false;
            }
            if(that.ruleCompanyAuth.companyCode.length == 0) {
                     Notify({
                        message: '请填写纳税人识别号',
                        duration: 3000,
                        background: '#E6A23C'
                    });
                    return false;
                }else if(that.ruleCompanyAuth.companyCode.length != 15 && that.ruleCompanyAuth.companyCode.length != 18 && that.ruleCompanyAuth.companyCode.length != 20) {
                     Notify({
                        message: '纳税人识别号仅支持15、18、20位',
                        duration: 3000,
                        background: '#E6A23C'
                    });
                    return false;
                }else if(!(/^[A-Za-z0-9]+$/.test(that.ruleCompanyAuth.companyCode))){
                     Notify({
                        message: '纳税人识别号仅支持数字或字母',
                        duration: 3000,
                        background: '#E6A23C'
                    });
                    return false;
                }
            //  性别 男1  女2 
            var sex = ''
            if(that.ruleCompanyAuth.chanceUserSex == '男'){
                sex = 1
            }else if(that.ruleCompanyAuth.chanceUserSex ==  '女') {
                sex = 2
            }else {
                sex = 0
            }

            that.axios(that.qdsGlobalApiUrl + 'updateChannelUserInfo?token=' + token,{
                        method: 'post',
                        params: {
                            userName: that.ruleCompanyAuth.userDataName,
                            gender: sex,
                            position: that.ruleCompanyAuth.userDataPost,
                            email: that.ruleCompanyAuth.userDataEmail,
                            wechatName: that.ruleCompanyAuth.userDataWx,
                            qqName: that.ruleCompanyAuth.userDataQq,
                            blogName: that.ruleCompanyAuth.userDataSinaWb
                        },
                        timeout: 10000
                    })
                    .then(response=> {
                        if(response.data.code == "200") {
                            // that.getUserBasicData();
                            // that.$message({
                            //     message: '保存成功',
                            //     type: 'success',
                            //     duration: 2000
                            // })
                        }else {
                             Notify({
                                message: '基础信息保存失败',
                                duration: 3000,
                                background: '#FF6262'
                            });
                        }
                        that.accountBasicSave()
                    })
                    .catch(error => {
                        that.accountBasicSave()
                    })
        },
        accountBasicSave() {        //保存发票信息
            var token = Util.getCookie('qdsMToken');
            var that = this;
            that.axios(that.qdsGlobalApiUrl + 'addChannelUserInvoice?token=' + token, {
                methods: 'post',
                params: {
                    companyName:  that.ruleCompanyAuth.companyName,
                    companyCode: that.ruleCompanyAuth.companyCode,
                    companyAddress: that.ruleCompanyAuth.companyAddress,
                    companyPhone: that.ruleCompanyAuth.companyPhone,
                    companyStartBank: that.ruleCompanyAuth.companyStartBank,
                    companyBankAccount: that.ruleCompanyAuth.companyBankAccount,
                },
                timeout: 10000
            })
            .then((response) => {
                if(response.data.code == "200") {
                    that.btnText = '编辑'
                    Notify({
                        message: '保存成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    that.getChannelUserInvoice()
                }else {
                    Notify({
                        message: '保存发票信息失败',
                        duration: 2000,
                        background: '#FF6262'
                    });
                    that.btnText = '编辑'
                }
                 that.disabled = {         // 如果该企业在该企业库中存在, 则后端返回企业信息, 可能存在空字段, 空字段可以修改, 不为空则不可修改
                    userDataName: true,    // 姓名
                    chanceUserSex: true,    // 性别
                    userDataPost: true,     // 职位
                    userDataEmail: true,     // email
                    userDataQq: true,       // QQ
                    userDataWx: true,
                    userDataSinaWb: true,    // 新浪微博

                    companyName: true,    // 公司名称
                    companyCode: true,    // 税号
                    companyAddress: true,   // 公司地址
                    companyStartBank: true,   // 开户行
                    companyBankAccount: true,   // 开户行账号
                }
                // that.getUserBasicData()
                window.history.back(-1)
            })
            .catch((error) => {
                 that.disabled = {         // 如果该企业在该企业库中存在, 则后端返回企业信息, 可能存在空字段, 空字段可以修改, 不为空则不可修改
                    userDataName: true,    // 姓名
                    chanceUserSex: true,    // 性别
                    userDataPost: true,     // 职位
                    userDataEmail: true,     // email
                    userDataQq: true,       // QQ
                    userDataWx: true,
                    userDataSinaWb: true,    // 新浪微博

                    companyName: true,    // 公司名称
                    companyCode: true,    // 税号
                    companyAddress: true,   // 公司地址
                    companyStartBank: true,   // 开户行
                    companyBankAccount: true,   // 开户行账号
                }
                // that.getUserBasicData()
                window.history.back(-1)
            })
        },
 	}
}
</script>

<style scoped>
    .item-file {
        border-bottom: 1px solid #e5e5e5;
        margin-left: 15px;
        position: relative;
    }
    .item-file .item-file-child {
        height: 2.75rem;
        width: 65%;
        position: absolute;
        top: 0px;
        left: 6.25rem;
        background: rgba(255,255,255,.1);
        z-index: 1;
    }
    .newCustomer-page {
        position: relative;
    } 
    .post-head{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2.6875rem;
        border-bottom: 1px solid #F5F5F5;
        background-color: #fff;
    }
    .back{
        position: absolute;
        left: .9375rem;
        display: inline-block;
        width: .5625rem;
        height: 1.1875rem;
        background: url(../../assets/post/back_icon.png);
        background-size: 100% 100%;
        cursor: pointer;
    }
    .head-font{
        font-size: 1rem;
        color: #0B231F;
        font-weight: bold;
    }  
    .delete{ 
        z-index: 9999;
        position: absolute;
        bottom: -18.75rem;
        right: 46%;
        color: #fff;
        background: cyan;
        font-size: 1.875rem;
    }
    .zhanwei {
        width: 100%;
        height: 2.4375rem;
    }
    .zhanwei-top {
        width: 100%;
        height: .9375rem;
    }
    .zhanwei-bottom {
        width: 100%;
        height: 5rem;
    }
    .infor-write {
        padding-right: .9375rem;
        height: 100%;
        background: #fff;
    }
    .text-box .textarea-tips {
        height: 2.0625rem;
        line-height: 2.0628rem;
        width: 21.5625rem;
        border: 1px solid #e5e5e5;
        border-bottom-right-radius:.8em;
        border-bottom-left-radius: .8em;
        margin: 0 auto;
    }
    .text-box .textarea-tips p{
        font-size: .75rem;
        color: #818D8D;
        padding-left: .9375rem;
    }
    .text_area {
        border-top-left-radius:.8em;
        border-top-right-radius:.8em;
        margin: 0 auto;
        height: 8.0625rem;
        width: 21.5625rem;
    }
    
    .next-one {
        position: fixed;
        bottom: 1px;
        padding: 0 .9375rem;
        background: #fff;
        border-top: 1px solid #e5e5e5;
    }
    .next-text {

        margin: 1.875rem .9375rem;
    }
    .hidden-input {
        display: none;
    }
    .next-one p{
        width: 21.5625rem;
        height: 2.75rem;
        line-height: 2.75rem;
        text-align: center;
        font-size: 1rem;
        color: #fff;
        margin: .4375rem 0;
        background: #FF963D;
        border-radius: 1.375rem;
        letter-spacing: .2em;
    }
    .next-text p{
        width: 21.5625rem;
        height: 2.75rem;
        line-height: 2.75rem;
        text-align: center;
        /* padding: 0 9.3125rem; */
        font-size: 1rem;
        color: #fff;
        background: #FF963D;
        border-radius: 1.375rem;
    }
    .newCustomer-page .stepTwo {
        width: 100%;
        background: #fff;
    }
    .bg {
        width: 100%;
        height: .625rem;
        background: #e5e5e5;
        /* margin-top: .625rem; */

    }
    /* .weibo {
        border: none;
    } */
    .newCustomer-page .weibo:not(:last-child)::after {
         border: none;
    }
    .bg-title {
        width: 100%;
        /* margin-top: .625rem; */

    }
    .bg-title p{
        font-size: 1rem;
        color: #0B231F;
        width: 100%;
        line-height: 2.75rem;
        border-bottom: 2px solid #e5e5e5;
        margin-top: .625rem;
        margin-left: .9375rem;
        margin-right: .9375rem;
    }

    .fixed-pop {
        position: fixed;
        left: 0px;
        bottom: 0px;
        width: 100%;
        z-index: 2;
    }
   

</style>




